.header {
  position: absolute;
  display: flex;
  align-items: center;
  z-index: 10;
  width: 100%; }
  .header .logo {
    background: #272C35; }
  .header_links {
    width: 100%;
    display: flex;
    justify-content: space-between; }
    .header_links__block {
      display: flex;
      padding: 0 50px; }
      .header_links__block a {
        margin-right: 30px;
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 23px;
        text-align: center;
        text-transform: uppercase; }
  .header__download {
    background: #272C35;
    padding: 20px;
    min-width: 300px;
    text-align: center; }
    .header__download a {
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF; }
  .header_hamburger {
    overflow: hidden;
    position: relative;
    width: 55px;
    height: 55px;
    background: #030E1E;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .header_line {
    transition: all .2s;
    position: absolute;
    width: 31px;
    height: 3px;
    border-radius: 18px;
    margin: 4px 0;
    cursor: pointer;
    background: #FFFFFF; }
    .header_line_1 {
      top: 16px;
      left: 20px; }
    .header_line_2 {
      top: 24px;
      left: 20px; }
    .header_line_3 {
      top: 32px;
      left: 20px; }
  .header.active .header_line_1 {
    top: 24px;
    left: 20px;
    transform: rotate(45deg); }
  .header.active .header_line_2 {
    left: -100px; }
  .header.active .header_line_3 {
    top: 24px;
    left: 20px;
    transform: rotate(-45deg); }
  .header.active .header_mob_nav {
    opacity: 1;
    left: 0;
    top: 55px;
    z-index: 3; }
