.product__title {
  font-style: normal;
  font-weight: 900;
  font-size: 100px;
  line-height: 126px;
  text-transform: uppercase;
  text-align: center; }

.product_card__img {
  max-width: 384px;
  max-height: 384px; }
  .product_card__img img {
    width: 100%;
    height: 100%; }

.product_card__title {
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 130%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.759431px;
  color: #302929; }

.product_category {
  display: flex;
  justify-content: space-between; }
  .product_category__item {
    margin-top: 40px;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 19px;
    text-transform: uppercase;
    color: #272C35;
    padding: 20px;
    background: #F4F4F4;
    transition: 2s all;
    cursor: pointer; }
    .product_category__item:hover {
      background: linear-gradient(80deg, #272C35 40%, #F4F4F4 40% 100%);
      color: white;
      animation: background 1s; }

@keyframes background {
  0% {
    background-position: -100px 0px; }
  100% {
    background-position: 0px 0px; } }
