@charset "UTF-8";
.swiper-pagination-progressbar {
  width: 25%;
  height: 2px;
  background: #DCDCDC; }
  .swiper-pagination-progressbar-fill {
    background-color: #000000; }

.swiper-pagination-current,
.currentZero {
  font-style: normal;
  font-weight: 500;
  font-size: 80px;
  line-height: 99px;
  text-align: center;
  color: #272C35; }

.swiper-pagination-total,
.totalZero {
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 50px;
  text-align: center;
  color: #BDBDBD; }

.top_slider-pagination {
  bottom: 8%; }

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background-color: #000000; }

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 86%;
  width: 56px;
  height: 56px; }
  .swiper-button-next:after,
  .swiper-button-prev:after {
    font-size: 48px;
    color: #FFFFFF; }

.swiper-button-next {
  right: 0; }
  .swiper-button-next:after {
    content: '🠒'; }

.swiper-button-prev {
  left: 55%; }
  .swiper-button-prev:after {
    content: '🠐'; }
