.innovation {
  padding-top: 30px;
  background: #E6E6E6; }
  .innovation__title-1 {
    padding-top: 30px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #272C35;
    margin: 0 auto;
    width: 80%; }
  .innovation__columns {
    display: flex; }
  .innovation__column {
    width: 20%;
    margin-top: 50px; }
  .innovation__img {
    width: 60%;
    position: relative; }
    .innovation__img .img_2 {
      position: absolute;
      right: 10%;
      bottom: 5%; }
  .innovation_item {
    margin-bottom: 30px; }
    .innovation_item__num {
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      text-transform: uppercase;
      color: #272C35;
      position: relative;
      margin-bottom: 10px; }
      .innovation_item__num::after {
        content: "";
        position: absolute;
        top: 15px;
        left: 40px;
        width: calc(100% - 40px);
        height: 2px;
        background: #F0493E; }
    .innovation_item__title {
      font-style: normal;
      font-weight: 900;
      font-size: 30px;
      line-height: 38px;
      text-transform: uppercase;
      color: #000000;
      margin-bottom: 10px; }
    .innovation_item__description {
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      color: #272C35; }
