.modal__block {
  display: none;
  transition: all .2s;
  padding: 50px 60px;
  width: 100%;
  overflow-y: auto;
  max-height: 100%;
  max-width: 500px;
  background: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  border-radius: 8px;
  transform: translate(-50%, -50%);
  -webkit-box-shadow: 14px 10px 13px -6px #bdbdbd;
  -moz-box-shadow: 14px 10px 13px -6px #bdbdbd;
  box-shadow: 14px 10px 13px -6px #bdbdbd; }
  .modal__block.active {
    display: block;
    z-index: 9999; }
  .modal__block .validation_error {
    bottom: -20px;
    left: 10px; }

.modal_close {
  transition: all .2s;
  color: #0000007a;
  cursor: pointer;
  display: flex;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 20px;
  right: 20px; }
  .modal_close:hover {
    color: #73CAC2; }

.modal_svg {
  margin-right: 8px; }

.modal_title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 40px; }
  .modal_title img {
    margin-right: 20px; }

.modal_form {
  margin-top: 40px; }

.modal_forget_btn {
  display: block;
  width: 100%;
  margin: 30px 0;
  cursor: pointer;
  text-align: center;
  text-decoration-line: underline; }

.modal_btn__block {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between; }
  .modal_btn__block .register {
    border: 1px solid; }

.modal__btn-center {
  display: flex;
  justify-content: center; }

.modal_new_user {
  text-transform: uppercase; }

.modal .mailing {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px; }
  .modal .mailing_choice {
    display: flex; }
    .modal .mailing_choice__item {
      margin-right: 20px; }

.modal_auth-social {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .modal_auth-social__title {
    color: #828282;
    font-size: 14px; }
  .modal_auth-social a {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: black; }
    .modal_auth-social a img {
      margin-right: 5px; }

@media (max-width: 480px) {
  .modal_btn__block {
    flex-direction: column; }
    .modal_btn__block .modal_btn {
      width: 100%;
      margin-bottom: 10px; }
    .modal_btn__block .modal_open {
      width: 100%; }
  .modal_title {
    justify-content: flex-start;
    padding-right: 30px; }
  .modal__block {
    padding: 15px; }
  .modal_form {
    padding-top: 0px;
    margin-top: 10px; } }

.input_section {
  margin-bottom: 30px; }
  .input_section__title {
    color: #333333;
    font-size: 14px;
    margin: 0 0 5px 25px; }
  .input_section input {
    width: 100%;
    border: 1px solid #E0E0E0;
    border-radius: 8px;
    box-sizing: border-box;
    height: 48px;
    padding: 15px 24px; }
  .input_section textarea {
    width: 100%;
    border: 1px solid #F2F2F2;
    padding: 15px 24px;
    box-sizing: border-box; }
