@charset "UTF-8";
.top {
  display: flex; }
  .top_slider {
    width: 80%;
    display: flex; }
    .top_slider_wrapper {
      padding-right: 50px; }
    .top_slider__slide {
      width: 100%;
      display: flex; }
    .top_slider-left {
      width: 55%;
      background-blend-mode: lighten;
      text-align: end; }
    .top_slider-right {
      width: 45%;
      background-blend-mode: lighten; }
    .top_slider__title {
      padding: 20% 5%; }
      .top_slider__title-2 {
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: 1px;
        color: #272C35; }
    .top_slider_btn {
      margin-top: 50px; }
      .top_slider_btn a {
        margin-right: 50px; }
  .top_social {
    width: 20%;
    display: flex;
    align-items: center;
    position: relative; }
    .top_social_links {
      margin-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 500px; }
      .top_social_links a {
        display: block;
        transform: rotate(-90deg);
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        color: #272C35; }
        .top_social_links a::before {
          content: '•';
          color: #272C35;
          position: relative;
          left: -30px; }

.btn_1 {
  padding: 22px 48px;
  gap: 10px;
  background: #272C35;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF; }

